#card {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 50px 0 100px 0;
}

.box {
  margin: 2rem;
  width: 28%;
}

.box img {
  border-radius: 2rem;
  height: 48vh;
  width: 100%;
}

.box p {
  font-size: 1.3rem;
  font-weight: 600;
  text-align: center;
  margin: 1.2rem;
}
.p{
  font-size: 1.2rem;
  font-style: italic;
  font-weight: 600;
  margin: 50px auto;
  text-align: center;
}

.link{
  color: rgb(241, 51, 51);
  padding: 0 10px;
  font-weight: bold;
}

@media only screen and (max-width:450px) {
  .box {
    margin: 1rem;
    width: 55%;
  }
  .box img {
    border-radius: 2rem;
    height: 33vh;
    width: 100%;
  }
}

@media only screen and (min-width:450px) and (max-width:800px) {
  .box {
    margin: 1rem;
    width: 40%;
  }
  .box img {
    border-radius: 2rem;
    height: 40vh;
    width: 100%;
  }
}